@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: rgb(22,36,71);
  background: linear-gradient(66deg, rgba(22,36,71,1) 0%, rgb(21, 56, 146) 100%);
  background-repeat: no-repeat;

  /* height: 100vh; */
  width: 100%a;
}

button,
input {
  font-family: 'Ubuntu', sans-serif;
}

::-webkit-scrollbar {
  width: 8px;
  }
  
  ::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #e43f5a; 
  }
  
  ::-webkit-scrollbar-thumb {
  background:#e43f5a; 
  }
.App {
  height: 100vh;
  width: 100%;

  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.selected {
  color: #3fe463 !important;
  border-bottom: 4px solid #3fe463 !important;
  font-size: 1.7rem !important;
}

.selected span {
  color: #3fe463 !important;
  text-shadow: 0px 0px 1px #3fe463 !important;
}

.selected span:hover {
  text-shadow: 0px 0px 5px #3fe463 !important;
}

@media (max-width: 767px) {
  .App {
    flex-direction: column-reverse;
  }
} 
.Button button {
  background-color: transparent;
  /* background: linear-gradient(66deg, rgba(22,36,71,1) 0%, rgb(21, 56, 146) 100%); */
  cursor: pointer;
  outline: none;
  border: none;
  color: white;
  width: 275px;
  height: 275px;
  border-radius: 100%;
  box-shadow: 0px 0px 20px -5px #e43f5a;
  font-size: 2.3rem;
  border: 4px solid #e43f5a;
  transition: all 0.2s;
}

.Button button:hover {
  box-shadow: 0px 0px 25px 3px #e43f5a;
}

.Button button:active {
  box-shadow: 0px 0px 25px 3px #fff;
  border: 4px solid #fff;
}


.Button button:active {
  /* background: linear-gradient(246deg, rgba(22,36,71,1) 0%, rgb(21, 56, 146) 100%); */
}

@media (max-width: 767px) {

  .Button button{
    width: 200px;
    height: 200px;
    font-size: 1.9rem;
  }

}
.Sorter {
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: top;
  /* align-items: center; */
}

.Sorter p {
  margin-bottom: 32px;
  color: #e43f5a;
  text-shadow: 0px 0px 1px #e43f5a;
  cursor: pointer;
  text-align: right;
  font-size: 1.4rem;
}

.Sorter p:hover {
  text-shadow: 0px 0px 4px #e43f5a;
}

.Sorter .flex{
  display: flex;
  width: 100%;
  margin-bottom: 8px;
}

.Sorter .flex button {
  min-width: 150px;
  width: 33%;
  height: 64px;
  color: #fff;
  font-size: 0.9rem;
  border-top-right-radius: 32px;
  border-bottom-right-radius: 32px;
  cursor: pointer;
  box-shadow: 0px 0px 20px -5px #e43f5a;

  border-left: 1px solid #e43f5a;

  border-top: 4px solid #e43f5a;
  border-right: 4px solid #e43f5a;
  border-bottom: 4px solid #e43f5a;
  background: transparent;

  transition: all 0.2s;
}

.Sorter .flex button:hover {
  box-shadow: 0px 0px 25px 3px #e43f5a;
}

.Sorter .flex button:active {
  box-shadow: 0px 0px 25px 3px #fff;
  border: 4px solid #fff;
  border-left: 1px solid #fff;
}

.Sorter .flex input {
  min-width: 300px;
  width: 67%;
  height: 64px;
  padding: 16px;
  color: #fff;
  font-size: 1rem;
  border-top-left-radius: 32px;
  border-bottom-left-radius: 32px;
  box-shadow: 0px 0px 20px -5px #e43f5a;

  border-right: none;

  border-top: 4px solid #e43f5a;
  border-left: 4px solid #e43f5a;
  border-bottom: 4px solid #e43f5a;
  background: transparent;

  transition: all 0.2s;
}

::-webkit-input-placeholder {
  color: #ddd;
}

:-ms-input-placeholder {
  color: #ddd;
}

::-ms-input-placeholder {
  color: #ddd;
}

::placeholder {
  color: #ddd;
}

.Sorter .flex input:hover {
  box-shadow: 0px 0px 25px 3px #e43f5a;
}

.Sorter .flex input:focus {
  box-shadow: 0px 0px 25px 3px #e43f5a;
}

.Sorter ol {
  color: #fff;
  font-size: 1.4rem;
  overflow:auto;
}

.Sorter ol li {
  margin: 8px 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 4px solid #e43f5a;
  padding: 4px;
}

.Sorter ol li span {
  margin-left: 16px;
  color: #e43f5a;
  text-shadow: 0px 0px 1px #e43f5a;
  cursor: pointer;
}

.Sorter ol li span:hover {
  text-shadow: 0px 0px 4px #e43f5a;
}

@media (max-width: 500px) {
  .Sorter .flex button {
    min-width: 20px;
  }

  .Sorter .flex input {
    min-width: 40px;
  }

  .Sorter {
    padding: 0 8px;
  }
}
